export default {
     onShareAppMessage: function(ops) {
    if (ops.from === "button") {
      // 来自页面内转发按钮
      console.log(ops.target);
    }
    return {
      title: "大可爱",//这里是定义转发的标题
      path: `pages/index/index`,//这里是定义转发的地址
      success: function(res) {
        // 转发成功
        console.log("转发成功:" + JSON.stringify(res));
        var shareTickets = res.shareTickets;
      },
      fail: function(res) {
        // 转发失败
        console.log("转发失败:" + JSON.stringify(res));
      }
    };
  },
  data () {
    return {
        listDetail:[
            {
                title:'网易云热评',
                imgInfo:require('../../static/image/happy/火 热.png'),
                jumpUrl:'../wangyi/wangyi'
            },
            {
                title:'笑话合集',
                imgInfo:require('../../static/image/happy/历史.png'),
                jumpUrl:'../xiaoHua/xiaoHua'
            },
            {
                title:'心灵毒鸡汤',
                imgInfo:require('../../static/image/happy/909.png'),
                 jumpUrl:'../jiTang/jiTang'
            },
            {
                title:'每日一文',
                imgInfo:require('../../static/image/happy/火 热.png'),
                jumpUrl:'../day/day'
            },
        ],
        canIUse: wx.canIUse('button.open-type.getUserInfo'),
        nickName:'',
        avatarUrl:'',
        powerStatus:true,
        jiantou:require('../../static/image/happy/jiantou.png'),
        shareImg:require('../../static/image/happy/share.png')
    }
  },
  components: {
  },

onLoad(){
    
  
  },
  methods: {
      jumpInfo(params){
        
          uni.navigateTo({url: params})
      },
    // 初始化判断是否授权
    initInfo(){
      var that =this;
       wx.getSetting({
        success: function(res){
          console.log(res)
          if(res.authSetting['scope.userInfo']){
            wx.getUserInfo({
              success: function(res) {
                console.log(res.userInfo)
                that.nickName = res.userInfo.nickName;
                that.avatarUrl = res.userInfo.avatarUrl;
                that.powerStatus =false;
                 that.$store.commit('changeLogin',1);
              }
            })
          }else{
            wx.showToast({
              title: '请先授权',
              icon: 'success',
              duration: 2000
            })
          }
        }
      })
    },
    getUserInfo1(){
      if(wx.canIUse('button.open-type.getUserInfo')){
      }else{
        console.log('请升级qq版本')
      }
    },
    bindGetUserInfo(e) {
      if (e.mp.detail.rawData){
        console.log(e.mp.detail.rawData)
        let infolist =JSON.parse(e.mp.detail.rawData);
        console.log(infolist)
        this.nickName = infolist.nickName;
        this.avatarUrl = infolist.avatarUrl;
        this.powerStatus =false;
         this.$store.commit('changeLogin',1);
      } else {
        //用户按了拒绝按钮
        this.powerStatus =true;
        wx.showToast({
            title: '拒绝授权',
            icon: 'success',
            duration: 2000
        })
      }
    }
  
  },
  mounted(){
      this.initInfo();
  }
}
